<template>
    <div v-if="smoothie" class="edit-smoothie container" >
        <h2>Edit {{smoothie.title}} Smoothie</h2>
        <form @submit.prevent="editSmoothie">
        <div class="field title">
           <label for="title">smoothie title </label>
               <input type="text" name="title" v-model="smoothie.title">
        </div>
        <div v-for="(ing,index) in smoothie.ingredients" :key="index" class="field">
            <label for="ingredient">add an ingredient</label>
            <input type="text" name="ingredient" v-model="smoothie.ingredients[index]">
            <i class="material-icons delete" @click="deleteing(ing)">highlight_off</i>
        </div>
        <div class="field add-ingredient">
            <label for="add-ingerdient">Add an ingredient</label>
            <input type="text" name="add-ingredient" @keydown.enter.prevent="adding" v-model="another">
        </div>
        <div class="center-align">
            <p v-if="feedback" class="red-text">{{feedback}}</p>
            <button class="btn pink">update smoothie</button>
        </div>
    </form>
    </div>
</template>
<script>
import db from "@/firebase/init";
import slugify from "slugify";
export default {
  name: "editsmoothie",
  data() {
    return {
      smoothie: null,
      another: null,
      feedback: null
    };
  },
  methods: {
    editSmoothie() {
      if (this.smoothie.title) {
        this.feedback = null;
        this.smoothie.slug = slugify(this.smoothie.title, {
          replacement: "-",
          remove: /[$*_+~.()'"!\-:@]/g,
          lower: true
        });
        db
          .collection("smoothies")
          .doc(this.smoothie.id)
          .update({
            title: this.smoothie.title,
            ingredients: this.smoothie.ingredients,
            slug: this.smoothie.slug
          })
          .then(() => {
            this.$router.push({ name: "index" });
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        this.feedback = "you must provide a smoothie title..pal";
      }
    },
    adding() {
      if (this.another) {
        this.smoothie.ingredients.push(this.another);
        this.another = null;
        this.feedback = null;
      } else {
        this.feedback = "you must had a n ingredient...pal";
      }
    },
    deleteing(ing) {
      this.smoothie.ingredients = this.smoothie.ingredients.filter(
        ingredient => {
          return ingredient != ing;
        }
      );
    }
  },
  created() {
    let ref = db
      .collection("smoothies")
      .where("slug", "==", this.$route.params.smoothie_slug);
    ref.get().then(snapshot => {
      snapshot.forEach(doc => {
        this.smoothie = doc.data();
        this.smoothie.id = doc.id;
      });
    });
  }
};
</script>
<style>
.edit-smoothie .delete {
  position: absolute;
  right: 0;
  bottom: 16px;
  color: #aaa;
  cursor: pointer;
}
.edit-smoothie {
  margin-top: 60px;
  padding: 20px;
  max-width: 500px;
}
.edit-smoothie h2 {
  font-size: 2em;
  margin: 20px auto;
}
.edit-smoothie .field {
  margin: 20px auto;
  position: relative;
}
</style>





